import { createApp } from 'vue'
import App from './App.vue'
import { createMetaManager } from 'vue-meta' // Forma correta para Vue 3
import router from './router'
import { createI18n } from 'vue-i18n'
import messages from './messages.js'

// Instancia o gerenciador de meta
const metaManager = createMetaManager()

const i18n = createI18n({
  legacy: false, // Use Composition API
  locale: 'en', // Define a língua inicial
  messages, // Registra as mensagens de tradução
});

createApp(App)
  .use(i18n)
  .use(router)
  .use(metaManager) // Usa o meta manager
  .mount('#app')
