import { createRouter, createWebHashHistory  } from 'vue-router'  // Altere para createWebHashHistory
import PrivacyPolicy from './components/PrivacyPolicy.vue'
import UnderConstruction from './components/UnderConstruction.vue'

// Importando os ícones por plataforma
import iosIcon from './assets/ios/apple-icon-57x57.png'
import androidIcon from './assets/android/android-icon-192x192.png'
import desktopIcon from './assets/desktop/favicon-96x96.png'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: UnderConstruction,
    meta: { 
      title: 'Black Paladins',
      icon: desktopIcon // Usando o ícone para desktop por padrão
    },
  },
  {
    path: '/renegado-privacy-policy',
    name: 'PrivacyPolicy',
    component: PrivacyPolicy,
    meta: { 
      title: 'Renegado - Privacy Policy',
      icon: desktopIcon // Usando o ícone para desktop por padrão
    },
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'NotFound',
    component: UnderConstruction,
    meta: { 
      title: 'Page Not Found',
      icon: desktopIcon // Usando o ícone para desktop por padrão
    },
  },
]

const router = createRouter({
  history: createWebHashHistory (), // Garante que o Vue Router funciona no GitHub Pages
  routes,
})


router.beforeEach((to, from, next) => {
  // Altera o título da página
  if (to.meta.title) {
    document.title = to.meta.title
  }

  // Detecta a plataforma (iOS, Android, Desktop)
  let icon;

  // Detecção da plataforma
  if (navigator.userAgent.match(/iPhone|iPad|iPod/)) {
    // Se for iOS
    icon = iosIcon;
  } else if (navigator.userAgent.match(/Android/)) {
    // Se for Android
    icon = androidIcon;
  } else {
    // Para desktop ou plataformas desconhecidas
    icon = desktopIcon;
  }

  // Atualiza o favicon
  const link = document.querySelector("link[rel*='icon']") || document.createElement('link');
  link.rel = 'icon';
  link.href = icon;

  // Verifica se o link já está presente e adiciona no head se necessário
  if (!document.head.contains(link)) {
    document.head.appendChild(link);
  }

  next();
})

export default router
